import http from '@/components/utils/http.js';
export default {
	data() {
		return {
			
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			cartList:[],
			goodprice:0,
			couponList: [
				{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressData: {
				name: '许小星',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '山东省济南市历城区',
				area: '149号',
				default: false,
			}
		}
	},
	onLoad(option){
		//商品数据
		if(option.data == undefined){
			this.$api.msg("传输数据有误");
			uni.navigateTo({
				url:"/pages/cart/home"
			})
		}
		let listdata= JSON.parse(option.data);
		let opts = {
			url: '/productApi/getcartlist/',
			method: 'post'
		};
		
		let param = listdata;
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
					let list = res.data["cartList"];
					
					let cartList = list.map(item => {
						item.images=this.apiServer+item.images;
						this.goodprice=this.goodprice+item.price*item.number;
						return item;
					});
					
					this.cartList = cartList;
					
				} else {
					uni.showToast({title: res.data.msg,icon: 'none'});
				}
			},
			error => {
				console.log(error);
			}
		);
		
		
		
	},
	methods: {
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type){
			this.payType = type;
		},
		submit(){
			uni.redirectTo({
				url: '/pages/money/pay'
			})
		},
		stopPrevent(){}
	}
}
